@import url('https://fonts.googleapis.com/css?family=Oswald|Roboto|Roboto+Condensed&display=swap');

$primary-color: #781820;
$font_family_1: 'Oswald', sans-serif;
$font_family_2: 'Roboto', sans-serif;

a,
a.nav-link {
  color: #781820;
  font-size: 1.5;
  font-family: $font_family_2;
}

#navbar-brand {
  color: $primary-color;
  font-family: $font_family_1;
  font-size: 2.5vw;
  font-weight: bold;
  margin: 2px;
  margin-right: 18px;
  padding: 2px;
}

.main-view {
  display: inline-block;
  background-color: white;
  justify-content: center;
}

.jumbotron-fluid {
  display: grid;
  background-color: white;
  justify-content: center;
  margin: 4px;
  padding: 4px;
}

.row {
justify-content: center 
}

.btn-primary {
  background-color: $primary-color;
  color: white;
  font-family: $font_family_1;
  text-align: center;
  font-size: 1.6vw;
  margin: 0px;
  padding: .375rem .75rem;
  vertical-align: middle;
  border: 0px;
  border-radius: .25rem;
}

.btn-primary-menu:hover, 
.btn-primary:hover {
  background-color: white;
  color: $primary-color;
  border: white;
}

.btn-primary:active,
.btn-primary-menu:active {
  background-color: $primary-color;
  color: white;
  border: none;
}

@media screen and (min-width: 1200px) {
  #navbar-brand h1 {
    font-size: 30px;
  }
  /*  title */
  #btn-primary,
  .btn-primary,
  .form-label,
  .genre-description,
  .movie-description,
  .director-bio,
  .text-muted {
    font-size: 24px;
  }

  /*  text */
  .form-control,
  .genre-description,
  .movie-description,
  .director-bio,
  .text-muted {
    font-size: 20px;
  }
}

@media screen and (max-width: 800px) {
  #navbar-brand h1 {
    font-size: 30px;
  }
  /*  title */
  #btn-primary,
  .btn-primary,
  .form-label,
  .form-control,
  .genre-name,
  .movie-title,
  .director-name {
    font-size: 16px;
  }

  /*  text */
  .genre-description,
  .movie-description,
  .director-bio,
  .text-muted {
    font-size: 14px;
  }
}

@media screen and (max-width: 600px) {
  a,
  .nav-link {
    color: $primary-color;
    font-family: $font_family_2;
  }

  p {
    font-size: 2.5vw;  
  }

  h1,
  .navbar-brand {
    font-size: 4vw;
  }

  .btn-delete,
  .btn-danger,
  .btn-primary {
    font-size: 2.5vw;
  }

  .form-control {
    font-size: 2vw;
  }

  .genre-name,
  .director-name,
  .profile-title,
  .card-title {
    font-size: 3vw;  
  }

  .form-label,
  .list-group-item{
    font-size: 2.5vw;
  }


  .director-bio,
  .movie-description
  .genre-description,
  .span-label,
  .span-value,
  .time {
    font-size: 2.5vw;
  }

  .text-muted {
    font-size: 2vw;
  }
}